.form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-top: 25px;
}

.form--input {
  height: 37px;
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 10px;
  border: 1px solid #B3B3B3;
  border-radius: 3px;
  font-size: 16px;
  color: #2B2F3E;
}

.form--input::-moz-placeholder {
  color: #B3B3B3;
}

.form--button {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 37px;
  margin-left: 10px;
  padding: 0 25px;
  border: 1px solid transparent;
  border-radius: 3px;
  font-size: 16px;
  cursor: pointer;
  white-space: nowrap;
  background-color: #27CCC0;
  color: #fff;
}


